/* apply.css */

body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
}

.container {
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.title {
    text-align: center;
    color: #333;
    margin-bottom: 20px;
    font-size: 24px;
}

.form {
    display: flex;
    flex-direction: column;
}
.form-group textarea {
    width: 100%; /* Set your desired fixed width */
    box-sizing: border-box; /* Ensures padding and border are included in the element's width */
    resize: vertical; /* Allows resizing vertically but not horizontally */
  }

.form-group {
    margin-bottom: 15px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
}

.form-group input[type="email"],
.form-group input[type="text"],
.form-group input[type="file"] {
    width: 100%;
    padding: 8px;
    box-sizing: border-box;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.file-name {
    margin-top: 5px;
    font-style: italic;
    color: #555;
}

.file-instructions {
    margin-top: 5px;
    font-size: 14px;
    color: #666;
}

.form-group input[type="submit"] {
    width: 100%;
    padding: 10px;
    background-color: #007BFF;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.form-group input[type="submit"]:hover {
    background-color: #0056b3;
}

.message {
    text-align: center;
    margin-top: 20px;
    color: #28a745; /* Green for success message */
    font-weight: bold;
}

/* Responsive design */
@media (max-width: 600px) {
    .container {
        padding: 15px;
    }

    .title {
        font-size: 20px;
    }

    .form-group input[type="email"],
    .form-group input[type="file"],
    .form-group input[type="submit"] {
        font-size: 14px;
    }
}
